import uniFab from '@/components/uni-fab/uni-fab.vue'
import url from "@/request/url.js"
import {
	mapState
} from 'vuex'
export default {
	data() {
		return {
			showTip: true,
			countDowns: [],
			cds: [],
			outCds: [],
			pattern: {
				color: '#7A7E83',
				backgroundColor: '#fff',
				selectedColor: '#007AFF',
				buttonColor: "#007AFF",
			},
			content: [{
					text: '倒计时',
					active: false,
					iconPath: '/static/image/icon/countdown.png',
					selectedIconPath: '/static/image/icon/countdown-select.png',
				},
				{
					text: '日记',
					active: false,
					iconPath: '/static/image/icon/diary.png',
					selectedIconPath: '/static/image/icon/diary-select.png',
				},
				{
					text: '未来信箱',
					active: false,
					iconPath: '/static/image/icon/mail.png',
					selectedIconPath: '/static/image/icon/mail-select.png',
				}
			]
		}
	},
	components: {
		uniFab
	},
	computed: {
		...mapState({
			userInfo: state => state.userInfo
		})
	},
	methods: {
		operate(id) {
			uni.showModal({
				content: "确认要删除这条倒计时吗？",
				success: res => {
					if (res.confirm) {
						uni.request({
							url: url.delCountDown + id,
							method: "DELETE",
							success: resp => {
								uni.showToast({
									title: '删除成功！'
								});
								this.getAllCountDown(response => {
									this.sort(response.data);
								});
							}
						});
					}
				}
			});
		},
		sort(data) {
			console.log(data)
			this.countDowns = data;
			this.cds.length = 0;
			this.outCds.length = 0;
			this.countDowns.forEach(cd => {
				if (cd.status > 0) {
					this.cds.push(cd);
				} else {
					this.outCds.push(cd);
				}
			});
		},
		getAllCountDown(callback) {
			uni.request({
				url: url.getAllCountDown,
				method: "GET",
				data: {
					uid: this.userInfo.id
				},
				success: callback
			})
		},
		trigger(e) {
			console.log(e);
			this.content[e.index].active = true;
			switch (e.index) {
				case 0:
					uni.navigateTo({
						url: 'add-countdown/add-countdown',
						animationType: "slide-in-right",
						animationDuration: 100
					})
					break;
				case 1:
					uni.navigateTo({
						url: 'add-diary/add-diary',
						animationType: "slide-in-right",
						animationDuration: 100
					})
					break;
				case 2:
					uni.navigateTo({
						url: 'add-mail/add-mail',
						animationType: "slide-in-right",
						animationDuration: 100
					})
					break;
			}
		}
	},
	onShow() {
		this.getAllCountDown(resp => {
			this.sort(resp.data);
			if (resp.data.length != 0) {
				this.showTip = false;
			}
		});
	},
	onPullDownRefresh() {
		this.getAllCountDown(resp => {
			this.sort(resp.data);
			uni.stopPullDownRefresh();
		});
	}
}
